@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;0,500;1,100;1,300;1,400;1,500&display=swap");

* {
  font-family: "Poppins", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-transform: capitalize;
  transition: all 0.2s linear;
  text-decoration: none;
}

html {
  font-size: 62.5%;
}

body {
  overflow-x: hidden;
}

.heading,
.clients .section-header h2,
.section-head h1,
.testimonials .section-header h2,
.section-head-1 h4 {
  margin: 2rem;
  padding-top: 6rem;
  display: inline-block;
  font-size: 3.5rem;
  color: #0e1011;
  position: relative;
  letter-spacing: 0.2rem;
}

.heading::before,
.heading::after,
.clients .section-header h2::before,
.clients .section-header h2::after,
.section-head h1::before,
.section-head h1::after,
.testimonials .section-header h2::before,
.testimonials .section-header h2::after,
.section-head-1 h4::before,
.section-head-1 h4::after {
  content: "";
  position: absolute;
  height: 2.5rem;
  width: 2.5rem;
  border-top: 0.4rem solid #0e1011;
  border-left: 0.4rem solid #0e1011;
}

.faq .heading::before,
.faq .heading::after {
  border-top: 0.4rem solid #00bfff;
  border-left: 0.4rem solid #00bfff;
}

.heading::before,
.clients .section-header h2::before,
.section-head h1::before,
.testimonials .section-header h2::before,
.section-head-1 h4::before {
  top: 5.8rem;
  left: -2rem;
}

.heading::after,
.clients .section-header h2::after,
.section-head h1::after,
.testimonials .section-header h2::after,
.section-head-1 h4::after {
  bottom: -0.5rem;
  right: -2rem;
  transform: rotate(180deg);
}

.row .btn {
  outline: none;
  border: none;
  border-radius: 5rem;
  background: white;
  border-style: groove;
  border-color: #002e5f;
  font-size: 1.5rem;
  cursor: pointer;
  height: 3.5rem;
  width: 15rem;
  box-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.3);
}

.communicate .btn {
  outline: none;
  border: none;
  border-radius: 5rem;
  background: white;
  border-style: groove;
  border-color: #002e5f;
  font-size: 1.5rem;
  cursor: pointer;
  height: 3.5rem;
  width: 15rem;
  box-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.3);
}

.row .btn:hover {
  letter-spacing: 0.1rem;
  opacity: 0.9;
  color: white;
  background: #002e5f;
}

.communicate .btn:hover {
  letter-spacing: 0.1rem;
  opacity: 0.9;
  color: white;
  background: #00bfff;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 1rem 2rem;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  position: fixed;
}

.header .logo img {
  width: 130%;
  height: 3rem;
  top: 0;
  left: 0;
}

.header .navbar ul {
  list-style-type: none;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.header .navbar ul li {
  margin: 0 1.5rem;
}

.header .navbar ul li a {
  font-size: 2rem;
  color: #fff;
}

.header .navbar ul li a:hover {
  color: #00bfff;
  text-decoration: underline;
}

.header .logo i {
  padding: 0.5rem;
}

.header .fa-bars {
  color: #fff;
  cursor: pointer;
  font-size: 3rem;
  display: none;
}

.home {
  min-height: 80vh;
  width: 100vw;
  background-image: url(../images/LP-Image.png);
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column;
  text-align: center;
  padding: 0 1rem;
  position: relative;
  overflow: hidden !important;
}

.home h1 {
  color: #fff;
  font-size: 5.5rem;
}

.home h2 {
  color: #fff;
  font-size: 3rem;
}

.home .wave {
  position: absolute;
  bottom: -0.5rem;
  left: 0;
  height: 11rem;
  width: 100%;
  background: url(../images/wave.png);
  background-size: 100rem 11rem;
  animation: waves 8s linear infinite;
  background-repeat: repeat-x;
}

.home .wave2 {
  animation-direction: reverse;
  animation-duration: 6s;
  opacity: 0.3;
}

.home .wave3 {
  animation-duration: 4s;
  opacity: 0.5;
}

@keyframes waves {
  0% {
    background-position-x: 0;
  }
  100% {
    background-position-x: 100rem;
  }
}

.about {
  min-height: 45vh;
  width: 100vw;
  top: -3rem;
  text-align: center;
  position: relative;
  overflow: hidden;
  line-height: 10rem;
}

.about .row {
  line-height: 4rem;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 0 4rem;
}

.about .row .content {
  text-align: center;
}

.about .row .content h3 {
  font-size: 3rem;
  color: black;
}

.about .row .content p {
  font-size: 2rem;
  color: #333;
  padding: 1rem 0;
}
.pt-5 .container .row .section-head p {
  font-size: 2rem;
  color: #333;
  padding: 1rem 0;
}
.section-head {
  margin-bottom: 60px;
  text-align: center;
  margin-top: -8rem;
}
.section-head p {
  font-size: 20px;
  line-height: 28px;
  text-align: center;
}
.item {
  background: #0e1011;
  text-align: center;
  padding: 30px 25px;
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.07);
  border-radius: 20px;
  margin-bottom: 30px;
  margin-top: -2rem;
  border: 5px solid rgba(0, 0, 0, 0.07);
  -webkit-transition: all 0.5s ease 0s;
  transition: all 0.5s ease 0s;
}
.item:hover {
  background: #525252;
  box-shadow: 0 8px 20px 0 rgba(0, 0, 0, 0.2);
  -webkit-transition: all 0.5s ease 0s;
  transition: all 0.5s ease 0s;
}
.item:hover .item,
.item:hover span.icon {
  background: #ffffff;
  border-radius: 10px;
  -webkit-transition: all 0.5s ease 0s;
  transition: all 0.5s ease 0s;
}
.item h6 {
  font-size: 2rem;
  color: #ffffff;
}
.item:hover h6,
.item:hover p {
  color: #ffffff;
  -webkit-transition: all 0.5s ease 0s;
  transition: all 0.5s ease 0s;
}
.item .icon {
  font-size: 40px;
  margin-bottom: 25px;
  color: yellow;
  width: 90px;
  height: 90px;
  line-height: 96px;
  border-radius: 50px;
}
.item .feature_box_col_one {
  background: rgba(247, 198, 5, 0.2);
  color: #52ab98;
}
.item .feature_box_col_two {
  background: rgba(255, 77, 28, 0.15);
  color: #52ab98;
}
.item .feature_box_col_three {
  background: rgba(0, 147, 38, 0.15);
  color: #52ab98;
}
.item .feature_box_col_four {
  background: rgba(0, 108, 255, 0.15);
  color: #52ab98;
}
.item .feature_box_col_five {
  background: rgba(146, 39, 255, 0.15);
  color: #52ab98;
}
.item .feature_box_col_six {
  background: rgba(23, 39, 246, 0.15);
