}

.team .row {
  display: inline-block;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.team .row .card {
  height: 35rem;
  width: 25rem;
  background-color: #fff;
  text-align: center;
  margin: 5rem 5rem;
  position: relative;
  overflow: hidden;
  -webkit-box-reflect: below 5px linear-gradient(transparent 70%, #0004);
  transition: 0.5s;
}

.team .row .card:hover {
  transform: translateY(-10px);
  cursor: pointer;
}

.team .row .card .image {
  margin: 1rem 0;
  padding-top: 4rem;
}

.team .row .card .image img {
  height: 13rem;
  width: 13rem;
  border-radius: 50%;
  border: 0.5rem solid #fff;
  box-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.3);
  object-fit: cover;
}

.team .row .card .info h3 {
  font-size: 2rem;
  color: #333;
}

.team .row .card .info span {
