}

.team .row .card .info .icons .fa-instagram:hover,
.footer .icons .fa-instagram:hover {
  color: #c13584;
  text-decoration: none;
}

.team .row .card .info .icons .fa-linkedin:hover,
.footer .icons .fa-linkedin:hover {
  color: #2867b2;
  text-decoration: none;
}

.team .row .card::before,
.team .row .card::after {
