  background: #efbb35;
  border: 1px solid #efbb35;
  letter-spacing: 0.2rem;
}

.footer .footer-top .footer-newsletter p {
  color: #ffffff;
  font-size: 14px;
}

.footer .credit,
.footer .copyright {
  text-align: center;
  padding-top: 30px;
}

@media (min-width: 768px) {
  .footer .credit {
    text-align: right;
  }

  .footer .copyright {
    text-align: left;
  }
}

.back-to-top {
  position: fixed;
  display: none;
  background-color: #00bfff;
  color: #ffffff;
  width: 45px;
  height: 45px;
  text-align: center;
  line-height: 1;
  font-size: 44px;
  right: 15px;
  bottom: 15px;
  transition: background 0.3s;
  z-index: 9;
}

.back-to-top i {
  color: #ffffff;
}

.back-to-top i:hover {
  color: black;
}

.faq {
  min-height: 70vh;
  width: 100vw;
  text-align: center;
  padding: 0 2rem;
  background: url(../images/faq.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.faq .row {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 2rem;
}

.faq .row .accordion-container {
  width: 50%;
  text-align: left;
}

.faq .row .accordion {
  margin-left: 1rem;
  margin-right: 2rem;
}

.faq .row .accordion-container .accordion .accordion-header {
  background-color: #00bfff;
  margin: 1rem 0;
  box-shadow: 0.1rem 0.1rem 0.3rem rgba(0, 0, 0, 0.3);
  cursor: pointer;
  margin-left: 1rem;
  margin-right: 2rem;
}

.faq .row .accordion-container .accordion .accordion-header span {
  display: inline-block;
  text-align: center;
  height: 4rem;
  width: 5rem;
  line-height: 4rem;
  font-size: 2rem;
  background: #333;
  color: #fff;
  clip-path: polygon(0% 0%, 75% 0%, 100% 50%, 75% 100%, 0% 100%);
}

.faq .row .accordion-container .accordion .accordion-header h3 {
  display: inline;
  color: #333;
  font-weight: 400;
  padding-left: 0.5rem;
  font-size: 1.5rem;
}

.faq .row .accordion-container .accordion .accordion-body {
  padding: 1rem;
  color: #444;
  box-shadow: 0.1rem 0.1rem 0.3rem rgba(0, 0, 0, 0.3);
  background-color: #fff;
  font-size: 1.3rem;
  display: none;
  margin-left: 1rem;
  margin-right: 2rem;
}

@media (max-width: 1200px) {
  .faq {
    min-height: 70vh;
  }
}

@media (max-width: 1000px) {
  html {
    font-size: 50%;
  }

  .header .logo img {
    width: 20%;
    height: 3rem;
    top: 0;
    left: 0;
    background-size: cover;
  }

  .header .fa-bars {
    display: block;
    color: white;
    margin-right: 1rem;
  }

  .header .fa-bars:hover {
    color: #00bfff;
  }

  .header .navbar {
    position: fixed;
    top: -120%;
    left: 0;
    height: auto;
    width: 100%;
    background-color: white;
    z-index: 1000;
    border-top: 0.1rem solid rgba(0, 0, 0, 0.3);
  }

  .header .navbar ul {
    height: 100%;
    width: 100%;
    flex-flow: column;
  }

  .header .navbar ul li {
    margin: 1rem 0;
  }

  .header .navbar ul li a {
    color: grey;
    font-size: 2.4rem;
  }

  .header .fa-times {
    transform: rotate(90deg);
  }

  .header .nav-toggle {
    top: 5.8rem;
  }

  .home h1 {
    color: #fff;
    font-size: 4rem;
  }

  .home h2 {
    color: #fff;
    font-size: 2rem;
  }

  .about {
    min-height: 38vh;
    width: 100vw;
    margin-top: -3rem;
    margin-bottom: -2rem;
    top: -2rem;
    text-align: center;
    position: relative;
    overflow: hidden;
    line-height: 6rem;
  }

  .about .row {
    line-height: 2rem;
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 0 4rem;
  }

  .about .row .content {
    text-align: center;
  }

  .about .row .content h3 {
    font-size: 2rem;
    color: var(--color);
  }

  .about .row .content p {
    font-size: 1.3rem;
    color: #333;
    padding: 1rem 0;
  }

  .clients {
    position: relative;
    padding: 90px 0;
    text-align: center;
    margin-top: -15rem;
    margin-bottom: -8rem;
  }

  .clients .section-header p {
    padding-bottom: 10px;
    margin-top: 2.5rem;
    text-align: center;
    font-size: 2rem;
  }

  .team {
    min-height: auto;
  }

  .team .row {
    flex-direction: column;
  }

  .contact {
    text-align: center;
    align-items: center;
  }

  .contact .heading {
    margin-bottom: 3rem;
    margin-top: -2rem;
  }

  .contact-in {
    width: 80%;
    height: auto;
    margin: auto auto 5rem auto;
    display: flex;
    flex-wrap: wrap;
    padding: 10px;
    border-radius: 10px;
    background: #fff;
    box-shadow: 0px 0px 10px 0px #666;
  }

  .contact-map {
    width: 100%;
    height: auto;
    flex: 50%;
  }
  .contact-map iframe {
    width: 100%;
    height: 100%;
  }
  .contact-form {
    width: 100%;
    height: auto;
    flex: 50%;
    text-align: left;
  }
  .contact-form-txt {
    width: 90%;
    height: 20px;
    color: #000;
    border: 1px solid #bcbcbc;
    border-radius: 50px;
    outline: none;
    margin-bottom: 20px;
    padding: 15px;
  }
  .contact-form-email {
    margin-left: 2rem;
    width: 90%;
    height: 20px;
    color: #000;
    border: 1px solid #bcbcbc;
    border-radius: 50px;
    outline: none;
    margin-bottom: 20px;
    padding: 15px;
  }
  .contact-form-txt::placeholder {
    color: #aaa;
    font-size: 1.5rem;
  }
  .contact-form-email::placeholder {
    color: #aaa;
    font-size: 1.5rem;
  }
  .contact-form-txtarea {
    margin-left: 2rem;
    width: 90%;
    height: 110px;
    color: #000;
    border: 1px solid #bcbcbc;
    border-radius: 10px;
    outline: none;
    margin-bottom: 20px;
    padding: 15px;
    font-family: "Poppins", sans-serif;
  }
  .contact-form-txtarea::placeholder {
    color: #aaa;
    font-size: 1.5rem;
  }

  .contact-form-btn {
    margin-left: 2rem;
    outline: none;
    border: none;
    border-radius: 5rem;
    background: white;
    border-style: groove;
    border-color: #002e5f;
    font-size: 1.5rem;
    cursor: pointer;
    height: 3.5rem;
    width: 12rem;
    box-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.3);
  }
  .contact-form-btn:hover {
    letter-spacing: 0.1rem;
    opacity: 0.9;
    color: white;
    background: #002e5f;
  }
  .contact-form-phone {
    margin-left: 2rem;
    width: 90%;
    height: 20px;
    color: #000;
    border: 1px solid #bcbcbc;
    border-radius: 50px;
    outline: none;
    margin-bottom: 20px;
    padding: 15px;
  }
  .contact-form-phone::placeholder {
    color: #aaa;
    font-size: 1.5rem;
  }

  .back-to-top {
    width: 30px;
    height: 30px;
    font-size: 30px;
  }

  .faq {
    padding: 0;
    min-height: 60vh;
  }

  .faq .row {
    padding: 0 1.5rem;
    flex-flow: column;
  }

  .faq .row .accordion-container {
    width: 100%;
  }

  .column {
    float: left;
    width: 33.33%;
    padding: 10px;
  }

  .row:after {
    content: "";
    display: table;
    clear: both;
  }
}
